<template>
  <div>
    <BottomNav :select="selected"></BottomNav>
    <!--swipe-->
    <yd-slider autoplay="4000">
      <yd-slider-item v-for="banner in bannerList" :key="banner.id">
        <img @click="goToPageBannerDetail(banner.id)" :src="banner.logoUrl" width="100%"/>
      </yd-slider-item>
    </yd-slider>
    <!--<mt-swipe :auto="4000" class="fustion_box">-->
      <!--<mt-swipe-item v-for="banner in bannerList" :key="banner.id"><img @click="goToPageBannerDetail(banner.id)"-->
                                                                        <!--:src="banner.logoUrl" width="100%"/>-->
      <!--</mt-swipe-item>-->
    <!--</mt-swipe>-->

    <!--nav-->
    <div class="home_nav">
      <mt-navbar class="fs-navbar">
        <mt-tab-item id="1">
          <div class="top_nav" @click="goToPageTaskList()">
            <span class="home_nav_span nav_msg_span">
              <i class="fa fa-bars fa-3x color_1" aria-hidden="true"></i>
              <mt-badge type="error" class="nav_msg" size="small"
                        v-if="acceptable.length > 0">{{acceptable.length}}</mt-badge>
            </span>
            <span class="fs-navbar-tit">红包奖励</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="2">
          <div class="top_nav" @click="goToPageChooseRankingProject()">
            <span class="home_nav_span"><i class="fa fa-trophy fa-3x color_2" aria-hidden="true"></i></span>
            <span class="fs-navbar-tit">销售排名</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="3">
          <div class="top_nav" @click="goToPageOrderInfo()">
            <span class="home_nav_span"><i class="fa fa-file-text-o fa-4x color_3" aria-hidden="true"></i></span>
            <span class="fs-navbar-tit">历史订单</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="4">
          <div class="top_nav" @click="goToPageMessageList()">
          <span class="home_nav_span nav_msg_span">
            <i class="fa fa-commenting fa-3x color_4" aria-hidden="true"></i>
            <mt-badge type="error" class="nav_msg" size="small"
                      v-if="unReads.length > 0 ">{{$store.state.messageTotal}}</mt-badge>
          </span>
            <span class="fs-navbar-tit">最新消息</span>
          </div>
        </mt-tab-item>
      </mt-navbar>
      <mt-navbar class="fs-navbar">
        <mt-tab-item id="5">
          <div class="top_nav" @click="goToPageChooseReportProject()">
            <span class="home_nav_span"><i class="fa fa-line-chart fa-3x color_3" aria-hidden="true"></i></span>
            <span class="fs-navbar-tit">统计报表</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="6">
          <div class="top_nav" @click="goToPageAdPage()">
            <span class="home_nav_span"><i class="fa fa-bookmark fa-3x color_2" aria-hidden="true"></i></span>
            <span class="fs-navbar-tit">延保资料</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="7">
          <div class="top_nav" @click="goToPageClassRoom()">
            <span class="home_nav_span"><i class="fa fa-video-camera fa-4x color_4" aria-hidden="true"></i></span>
            <span class="fs-navbar-tit">在线学习</span>
          </div>
        </mt-tab-item>
        <mt-tab-item id="8">
          <div class="top_nav" @click="goToPageSalesSkills()">
          <span class="home_nav_span nav_msg_span">
            <i class="fa fa-graduation-cap fa-3x color_1" aria-hidden="true"></i>
          </span>
            <span class="fs-navbar-tit">销售技巧</span>
          </div>
        </mt-tab-item>
      </mt-navbar>
    </div>

    <div v-if="unReads.length > 0">
      <div class="fv_title">消息列表</div>
      <router-view name="Message" class=""></router-view>
    </div>
    <div v-if="personTask.length > 0">
      <div class="fv_title">任务列表</div>
      <router-view name="TaskList" class="hm_taskList"></router-view>
    </div>
    <div v-if="unReads.length === 0 && personTask.length === 0 " class="null_data">
      <div class="null"></div>
      <p>亲，暂无内容哦~~</p>
    </div>
  </div>

</template>

<script>
  import BottomNav from './public/bottom_nav.vue'
  import {mapState, mapMutations, mapActions} from 'vuex'
  import {loadData} from '../common/utils'
  import _ from 'lodash'
  import moment from 'moment'

  export default {
    name: 'Home',
    components: {
      BottomNav
    },
    data () {
      return {
        selected: 'home',
        bannerList: [],
        page: 1,
        unRead: [],
        materialType: 3,
        acceptable: [],
        personTask: []
      }
    },
    async created () {
      this.getBannerList()
      if (_.isEmpty(this.bannerList)) {
        let par = {
          page: this.page,
          materialType: this.materialType
        }
        await loadData(() => {
          return this.getMaterials(par)
        }, null, null, true)
        this.getBannerList()
      }
      let param = {
        page: this.page,
        read: false
      }
      let msg = _.isEmpty(this.$store.state.messages)
      if (msg) {
        await this.getMessageList(param)
      }
      await loadData(() => {
        return this.getTasks()
      }, null, null, true)
    },
    computed: {
      unReads () {
        return _.filter(this.$store.state.messages, {status: false})
      },
      ...mapState('task', {
        storeAcceptable: state => state.taskList.acceptable,
        storePersonTask: state => state.taskList.personTask
      })
    },
    methods: {
      ...mapMutations('task', ['setTaskListNav']),
      ...mapActions(['getMaterials', 'getMessageList']),
      ...mapActions('task', ['getHomePersonTaskList', 'getTaskListAcceptable']),
      getAcceptable () {
        this.acceptable = JSON.parse(JSON.stringify(this.storeAcceptable))
        let arr = []
        _.forEach(this.acceptable, (item) => {
          if (!item.acceptEndTime && !item.acceptStartTime) {
            arr.push(item)
          } else {
            let more = true
            let less = true
            if (item.acceptEndTime) {
              less = moment().isBefore(item.acceptEndTime)
            }
            if (item.acceptStartTime) {
              more = moment().isAfter(item.acceptStartTime)
            }
            if (less && more) {
              arr.push(item)
            }
          }
        })
        this.acceptable = JSON.parse(JSON.stringify(arr))
      },
      async getTasks () {
        let taskA = this.storeAcceptable.length === 0
        if (taskA) {
          await this.getTaskListAcceptable()
        }
        this.getAcceptable()
        await this.getHomePersonTaskList()
        this.personTask = this.storePersonTask
      },
      getBannerList () {
        this.bannerList = _.filter(this.$store.state.materials, {materialTypeCode: 3})
      },
      goToPageMessageList: function () {
        this.$router.push({
          name: 'Message',
          query: {}
        })
      },
      goToPageTaskList: function () {
        this.setTaskListNav('acceptable')
        this.$router.push({
          name: 'TaskList',
          query: {}
        })
      },
      goToPageBannerDetail: async function (bannerId) {
        let res = _.find(this.$store.state.materials, {id: bannerId})
        if (res.contentType === '富文本') {
          this.$router.push({
            name: 'BannerDetails',
            query: {
              bannerId
            }
          })
        } else if (res.contentType === 'Url') {
          if (res.id === bannerId) {
            window.location.href = res.content
          }
        }
      },
      goToPageChooseReportProject: function () {
        this.$router.push({
          name: 'ChooseReportProject'
        })
      },
      goToPageAdPage: function () {
        this.$router.push({
          name: 'AdPage',
          query: {}
        })
      },
      goToPageClassRoom: function () {
        this.$router.push({
          name: 'Classroom',
          query: {}
        })
      },
      goToPageFaqList: function () {
        this.$router.push({
          name: 'FaqList',
          query: {}
        })
      },
      goToPageSalesSkills: function () {
        this.$router.push({
          name: 'SalesSkills',
          query: {}
        })
      },
      goToPageChooseRankingProject () {
        this.$router.push({
          name: 'ChooseRankingProject'
        })
      },
      goToPageOrderInfo () {
        this.$router.push({
          name: 'MyOrder',
          query: {}
        })
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import'../common/ydui.base.css';

  .home_nav {
    background: #ffffff;
  }

  .hm_message {
    max-height: 375px;
    overflow: hidden;
  }

  .hm_taskList {
    margin-bottom: 70px
  }

  .nav_msg_span {
    position: relative
  }

  .nav_msg {
    padding: 1px 4px;
    font-size: 12px;
    position: absolute;
    left: 20px;
    top: -2px;
  }

  .fa-4x {
    font-size: 1.8em;
    margin-top: 3px;
  }

  .home_nav_span {
    width: 2.5em;
    height: 2.2em;
    display: inline-block;
  }

  .home_task {
    margin-bottom: 70px;
  }

  .label span {
    margin-right: 4%;
  }

  .connected span {
    display: inline-block;
    width: 100%;
  }

  .fustion_box {
    width: 100%;
    height: 200px;
  }

  .fustion_box img {
    height: 200px;
  }

  .fs-navbar {
    z-index: 9999;
  }

  @media screen and (width: 768px) {
    .fustion_box {
      width: 100%;
      height: 350px;
    }

    .fustion_box img {
      height: 350px;
    }
  }

  @media screen and (width: 1024px) {
    .fustion_box {
      width: 100%;
      height: 450px;
    }

    .fustion_box img {
      height: 450px;
    }

  }

  .fs-navbar .fs-navbar-tit {
    display: block;
    padding-top: 5px;
  }

  .color_1 {
    color: #26a2ff;
  }

  .color_2 {
    color: #ffc107;
  }

  .color_3 {
    color: #FE633F;
  }

  .color_4 {
    color: #17B794;
  }

  .home_msg {
    margin-top: 10px;
    background: #ffffff
  }

  .home_msg .mint-cell {
    text-align: left;
  }

  .pro .t-c {
    color: #888;
    text-decoration: none;
  }

  .mint-tab-item-label .fs-navbar-tit {
    font-size: 14px;
  }

  .mint-navbar .mint-tab-item {
    padding: 0px;
  }

  .top_nav {
    padding: 10px 0
  }

  .mint-tab-item:active {
    background: #E5E5E5;
  }

  @media only screen and (max-width: 320px) and (max-height: 568px) {
    .fustion_box {
      height: 170px;
    }

    .fustion_box img {
      height: 170px;
    }
  }
</style>



